@import url(https://fonts.googleapis.com/css2?family=Caveat+Brush&display=swap);
:root {
  --primary: whitesmoke;
  --dark: #333;
  --light: #fff;
  --shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
}
html {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  color: #333;
  color: var(--dark);
}
body {
  background: #fff;
  background: var(--light);
  margin: 30px 50px;
  line-height: 1.4;
}
img {
  max-width: 100%;
}
@media (max-width: 700px) {
  body {
    background: #333;
    background: var(--dark);
    margin: 10px 10px;
    line-height: 1.4;
  }
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  box-shadow: var(--shadow);
}
.brand {
  background: url(/Expense-monitor/static/media/brand.b55e877b.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  box-shadow: var(--shadow);
}
.brand h1 {
  margin-top: 300px;
  color: var(--primary);
  font-size: 5rem;
}
.brand p {
  color: var(--primary);
  font-size: 2rem;
}
.wrapper {
  display: grid;
  grid-gap: 20px;
  padding: 4rem;
  background: var(--primary);
}

.top-container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}
.title-head {
  background: var(--dark);
  width: 75%;
  color: var(--light);
  text-align: center;
  font-size: 0.7rem;
  padding: 0.5rem;
  box-shadow: var(--shadow);
  text-transform: uppercase;
}

.title-sub {
  color: var(--dark);
  text-align: center;
  font-size: 1.3rem;
  padding: 0.2rem;
}

/* money box */

.money-box {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
}

.box-a {
  padding: 2rem;
}
.box-a img {
  width: 250px;
}

.box-b {
  padding: 2rem;
}
.allCost {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: 1fr;
  justify-items: center;
}
#totalcost {
  width: 100px;
  padding: 0.5rem;
  background: var(--light);
  border: 2px solid;
  color: var(--dark);
  font-size: 1rem;
}
button {
  background: var(--dark);
  padding: 0.5rem;
  font-size: 1.5rem;
  color: var(--light);
  box-shadow: var(--shadow);
  border: none;
}
.message p {
  font-size: 1.3rem;
  text-decoration: underline;
  font-family: "Caveat Brush", cursive;
  color: var(--dark);
  padding: 0.3rem 1rem;
  -webkit-text-decoration-style: dashed;
          text-decoration-style: dashed;
}

/* media queries */
@media (max-width: 700px) {
  .brand {
    display: none;
  }
  .container {
    grid-template-columns: 1fr;
    box-shadow: var(--shadow);
  }
  .top-container {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
  }
  .title-head {
    background: var(--dark);
    width: 100%;
    color: var(--light);
    font-size: 0.5rem;
    padding: 0.5rem;
    box-shadow: var(--shadow);
    text-transform: uppercase;
  }

  .title-sub p {
    color: var(--dark);
    text-align: center;
    font-size: 1.2rem;
    padding: 0.2rem;
  }
  .box-b {
    padding: 0.5rem;
  }
  .allCost {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 1fr;
    justify-items: center;
  }
  #totalcost {
    width: 100px;
    padding: 0.5rem;
    background: var(--light);
    border: 2px solid;
    color: var(--dark);
    font-size: 1rem;
  }
  .message p {
    font-size: 1.3rem;
    font-family: "Caveat Brush", cursive;
    color: var(-dark);
    padding: 0.3rem 1rem;
    text-align: center;
  }
}

.Manage-container {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-gap: 10px;
  background-color: var(--primary);
  box-shadow: var(--shadow);
  padding: 0.5rem;
}
/* filler  */
.filler {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0.8rem;
  justify-items: center;
  background-color: black;
  box-shadow: var(--shadow);
}
.progress-bucket {
  position: relative;
  margin-top: 200px;
  width: 4rem;
  height: 20rem;
  border: 4px solid black;
  background-color: whitesmoke;
  border-radius: 30px;
  box-shadow: 0 1px 20px rgba(238, 231, 231, 0.8);
  padding: 0;
  overflow: hidden;
}
.progress {
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0;
  width: 100%;
  height: 0;
  border-radius: 24px;
  transition: height 1s ease-in-out, background 1s ease-in-out;
  background-color: #2ecc40;
}

.progress[data-percent="fourth"] {
  height: 25%;
}
.progress[data-percent="half"] {
  height: 50%;
}
.progress[data-percent="extra"] {
  height: 75%;
}
.progress[data-percent="full"] {
  height: 100%;
}
.progress[data-percent="empty"] {
  height: 0%;
  background-color: black;
}
.progress[data-percent="over"] {
  height: 100%;
  background-color: crimson;
}
/* save */

#save {
  border: 2px dotted white;
  box-shadow: var(--shadow);
  padding: 0.5rem;
  color: whitesmoke;
}

/* cost box */
.cost-box {
  display: grid;
  grid-template-rows: 1fr;
  grid-gap: 10px;
  width: 100%;
}
.item-box {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 5px;
  justify-items: center;
}

.item-box-a {
  padding: 0.8rem;
  display: grid;
  grid-gap: 10px;
  width: 250px;
  grid-template-rows: 1fr;
  box-shadow: var(--shadow);
  border-radius: 10px;
}

.box-header img {
  height: 200px;
  width: 100%;
  object-fit: cover;
}

h2 {
  text-align: center;
  text-transform: uppercase;
  background-color: var(--dark);
  font-size: 1.5rem;
  width: 100%;
  padding: 0.5rem;
  color: var(--primary);
  box-shadow: var(--shadow);
  width: auto;
}
input {
  width: 30%;
  background-color: whitesmoke;
  color: var(--dark);
  padding: 0.8rem;
  border: 2px solid black;
  box-shadow: var(--shadow);
  font-size: 1.2rem;
}

/* bottom - container */
.bottom-container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}

p {
  font-size: 2rem;
}

/* media queries */
@media (max-width: 750px) {
  .Manage-container {
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-gap: 10px;
    background-color: var(--primary);
    box-shadow: var(--shadow);
    padding: auto;
  }

  .filler {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0rem;
    justify-items: center;
    background-color: black;
    box-shadow: var(--shadow);
  }
  .progress-bucket {
    position: relative;
    margin-top: 10px;
    width: 1rem;
    height: 40rem;
    border: 4px solid black;
    background-color: whitesmoke;
    border-radius: 30px;
    box-shadow: 0 1px 20px rgba(238, 231, 231, 0.8);
    padding: 0;
    overflow: hidden;
  }
  #save {
    font-size: small;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    transform: rotate(-90deg);
    border: none;
  }
  .item-box-a {
    padding: 0.8rem;
    display: grid;
    grid-gap: 10px;
    width: 220px;
    grid-template-rows: 1fr;
    box-shadow: var(--shadow);
    border-radius: 10px;
  }
  h2 {
    font-size: 1rem;
  }
  input {
    padding: 0.2rem;
  }
}

